// 
$(document).ready(function(){
    $('.wx-chat').on('click',function(){
        $('#form').addClass('on');
        $('.code-qq').removeClass('on');
        $('.wx-chat > img').attr('src', './images/login_wechat_selected.png');
        $('.p-word > img').attr('src', './images/login_account.png');
    })
    $('.p-word').on('click',function(){
        $('.code-qq').addClass('on');
        $('#form').removeClass('on');
        $('.wx-chat > img').attr('src', './images/login_wechat.png');
        $('.p-word > img').attr('src', './images/login_account_selected.png');
        
    })
    /*  */
    $('#form-input').bootstrapValidator({
        message: 'This value is not valid',
        //excluded:[":hidden",":disabled",":not(visible)"] ,//bootstrapValidator
        excluded: ':disabled',
        feedbackIcons: {
            valid: 'iconfont icon-zhengque',
            invalid: 'iconfont icon-cuowu',
            validating:'iconfont icon-cuowu'
        },
        fields: {
            username: {
                message: '',//
                validators: {
                    notEmpty: {
                        message: ''
                    },
                    stringLength: {
                        min: 2,
                        max: 30,
                        message: '230'
                    },
                    regexp: {
                        regexp: /^[a-zA-Z0-9_\.]+$/,
                        message: ''
                    },
                }
            },
            password:{
                message: '',//
                validators: {
                    notEmpty: {
                        message: ''
                    },
                    stringLength: {
                        min: 6,
                        max: 30,
                        message: '630'
                    },
                    regexp: {
                        regexp: /^[a-zA-Z0-9_\.]+$/,
                        message: ''
                    },
                }
            }
        }
    }).on('submit', function (e, data) {
        console.log(e)
    });
})


